@keyframes increaseFromCircle {
  0% {
    transform: translate(65%, 0);
    opacity: 0;
  }

  10% {
    transform: translate(65%, 0);
    opacity: 1;
  }

  50% {
    transform: translate(65%, 0);
    opacity: 1;
  }

  100% {
    transform: translate(0, 0);
    opacity: 1;
  }
}

@keyframes increaseFromCircleMobile {
  0% {
    transform: translate(85%, 0);
    opacity: 0;
  }

  10% {
    transform: translate(85%, 0);
    opacity: 1;
  }

  50% {
    transform: translate(85%, 0);
    opacity: 1;
  }

  100% {
    transform: translate(0, 0);
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes spinFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-200%) rotate(-60deg);
  }

  100% {
    opacity: 1;
    transform: translateX(0) rotate(0deg);
  }
}

@keyframes spinFromRight {
  0% {
    opacity: 0;
    transform: translateX(200%) rotate(60deg);
  }

  100% {
    opacity: 1;
    transform: translateX(0) rotate(0deg);
  }
}
